<template>
  <!-- App.vue -->
  <v-app id="app-main" class="primary">
    <v-app-bar elevation="0" app dark color="blue" class="border-radius">
      <v-app-bar-nav-icon icon>
        <v-icon>mdi-home</v-icon>
      </v-app-bar-nav-icon>
      <v-app-bar-title>登录授权中心</v-app-bar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>mdi-heart</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-main>
      <router-view></router-view>
    </v-main>
    <v-overlay
        ref="SpinnerLoading"
        :value="Loading"
        color="#E1F5FE"
        :z-index="6"
        :opacity="0.7"
    >
      <fulfilling-square-spinner
          :animation-duration="4000"
          :size="50"
          color="#ff1d5e"
      />
    </v-overlay>
    <v-snackbar v-model="snackbar.show" app :timeout="snackbar.timeout" :color="snackbar.color" :multi-line="true"
                :right="true">
      {{ snackbar.text }}
      <template v-slot:action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbar.show = false">关闭</v-btn>
      </template>
    </v-snackbar>
  </v-app>
</template>
<script>
import {FulfillingSquareSpinner} from "epic-spinners";

export default {
  components: {
    FulfillingSquareSpinner,
  },
  name: "App",
  data: () => ({
    Loading: false,
    snackbar: {
      show: false,
      code: 200,
      text: '请求错误',
      timeout: 2000,
      color: "red"
    }

  }),

  methods: {
    showLoading(isShow) {
      this.Loading = isShow;
    },

    showAxiosMessage(snackbar) {
      this.snackbar = snackbar;
    },
  },
  mounted() {
    this.$bus.$on("showLoading", this.showLoading);
    this.$bus.$on("showAxiosMessage", this.showAxiosMessage);

  },
};
</script>
