export const routes = [
    {
        path: '*',
        redirect: {
            path: '/404'
        }
    },
    {
        path: '/',
        redirect: {
            path: '/login'
        }
    },
    {
        path: '/404',
        name: 'NotFound',
        component: () => import( `@/views/NotFound.vue`)
    },
    {
        path: '/403',
        name: 'AccessDenied',
        component: () => import(`@/views/Deny.vue`)
    },
    {
        path: '/500',
        name: 'ServerError',
        component: () => import(`@/views/Error.vue`)
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import(`@/views/Login.vue`)
    },
    {
        path: '/register',
        name: 'Register',
        component: () => import(`@/views/Register.vue`)
    },
    {
        path: '/forget',
        name: 'PasswordForget',
        component: () => import(`@/views/PasswordForget.vue`)
    },
    {
        path: '/apps',
        name: 'AppList',
        component: () => import(`@/views/AppList.vue`)
    },

    {
        path: '/authorization',
        name: 'Authorization',
        component: () => import(`@/views/Authorization.vue`)
    },
]